<template>
  <div id="app">


      
<top :txt="txt"></top>
 
<div class="top_zhanwei_box"></div>	
 
<!--  -->
<div class="zy_module-content bgwh">	
	<div class="vip_user_recharge">
		<form>
			<p><a></a></p>
			<div class="zhanghao">
				<h3></h3>
				<input type="text" name="" value="" disabled="disabled">
			</div>
			<div class="zy_title_con">
				<h2><span><i></i></span></h2>
			</div>
			<ul class="dis_flex mb6">
				<li><label><input type="radio" name="money" value="200"><span>200</span></label></li>
				<li><label><input type="radio" name="money" value="500"><span>500</span></label></li>
				<li><label><input type="radio" name="money" value="1000"><span>1000</span></label></li>
				<li><label><input type="radio" name="money" value="2000"><span>2000</span></label></li>
				<li><label><input type="radio" name="money" value="5000"><span>5000</span></label></li>
				<li>
					<label>
						<input type="radio" name="money" onclick="write11()">
						<span><input id="shuru" type="text" name="" placeholder="1~20000"></span>
					</label>
				</li>
			</ul>
			<div><router-link to="user_recharge_know" style="color: #999; font-size: .22rem;"></router-link></div>
			<button id="now_cz"></button>
		</form>
		<!--  -->
		<div class="pay_popm_box">
			<div class="black_bg"></div>
			<div class="box bgwh">
				<h1><span class="close"></span></h1>
				<ul>
					<li><span></span><a></a></li>
					<li><span></span><a>ywm201808040405</a></li>
					<li><span></span><a>2018-08-04 20:01:59</a></li>
					<li><span></span><a>20.00</a></li>
				</ul>
				<div class="pay">
					<h2 class="bgf5 p_02"></h2>
					<a href="#"><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/vip_img/WeChat_pay.png"></a>
					<!-- <a href="#"><img src="images/vip_img/WeChat_pay.png"></a> -->
				</div>
			</div>
		</div>
		<!-- js -->
		 

	</div>
</div>

    
    
  </div>
</template>

<script>
import $ from 'jquery'
import Swiper from 'swiper'
import '../../assets/js/public.js'
import '../../../node_modules/swiper/dist/js/swiper.min.js'

import top from '../index/top.vue'
export default {
	components:{top},

data () {
    return {
			txt:''

}
  },
  mounted(){
     $(function(){
		var top_h=$(".vip_top_box").height()
		$(".top_zhanwei_box").css("height",top_h)
	});
    //
			function write11(){
				$("#shuru").focus()
			}

			//js	
			$(function(){
				$(".vip_user_recharge #now_cz").mousedown(
					function(){
						$(".vip_user_recharge .pay_popm_box").fadeIn(300)
						$(".vip_user_recharge .pay_popm_box .box").stop().animate({bottom:0},300)
					}
				)
				$(".vip_user_recharge .black_bg , .vip_user_recharge .close").mousedown(
					function(){
						$(".vip_user_recharge .pay_popm_box").fadeOut(300)
						$(".vip_user_recharge .pay_popm_box .box").stop().animate({bottom:-999},300)
					}
				)
			})
         

  }
}
</script>

<style scoped>
@import url('../../assets/css/index.css');
@import url('../../assets/css/public/public_lib.css');
@import url('../../assets/css/zy_public.css');
@import url('../../assets/css/zy_vip.css');


</style>



