<template>
  <div id="app">
    <div class="yh_select bgwh mb6">
		<div class="index_title rel">
			<h1>
				<router-link to=""></router-link>
			</h1>
			<i></i>
		</div>
		<div class="yh_goodsbox fix">
			<div class="l">
				<div class="swiper-container yh_wrapper">
					<ul class="swiper-wrapper">
						<li class="swiper-slide" v-for="(item,index) in rexiao" :key="index">
							<router-link  :to="item.url">
								<img v-lazy="item.img">
								<p>{{item.txt}}</p>
	            				<span><em>{{item.jiu}}</em><i>{{item.new}}</i></span>
							</router-link>
						</li>
						
					</ul>
					<div class="swiper-pagination sp02"></div>
			    </div>
		    </div>

		    <div class="two r">
		    	<h2>
		    		<router-link to="category"><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/index_ad_pic (2).jpg"></router-link>
		    	</h2>
		    	<h3>
		    		<router-link to="category"><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/index_ad_pic (3).jpg"></router-link>
		    	</h3>
		    	<h4>
		    		<router-link to="category"><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/index_ad_pic (6).jpg"></router-link>
		    	</h4>
		    </div>
		</div>
	</div>

  </div>
</template>

<script>
//

import Swiper from 'swiper'
export default {
  name: 'app',
  data () {
    return {
         rexiao:[{
            url:'category',
            img:'http://ydty.oss-cn-shanghai.aliyuncs.com/images/pic01.jpg',
            txt:'Classic al/ ',
            new:599.00,
            jiu:699.00
        },{
            url:'category',
            img:'http://ydty.oss-cn-shanghai.aliyuncs.com/images/pic02.jpg',
            txt:' =++ L(175)',
            new:199.00,
            jiu:399.00
        },{
            url:'category',
            img:'http://ydty.oss-cn-shanghai.aliyuncs.com/images/pic03.jpg',
            txt:'  1517104',
            new:199.00,
            jiu:399.00
        },],
      msg: 'Welcome to Your Vue.js App'
    }
  },
  mounted(){
      //swiper
            new Swiper('.yh_wrapper', {
					        pagination:{
										el:'.sp02'
									},
					        paginationClickable: true,
					        autoplay: {
                            delay: 2000,
                            stopOnLastSlide: false,
                             disableOnInteraction: true,
                             },
					        loop:"ture",
					        speed:500,
					        autoplayDisableOnInteraction : false,
                        });
  }
}
</script>

<style>
</style>



