<template>
  <div id="app">





      

<header class="zy_search_top_box">
	<div class="zy_search_top zy_title_top zy_supplier_top fix">
		<div class="one" href="javascript:" onclick="history.back();"><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/back_jt.png"></div>
		<div class="box">
			<span><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/icon_search.png"/></span>
		    <input type="text" placeholder="" value="" />
	    </div>
	    <div class="r">
	    	<span><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/web/zy_icon_menu.png"></span>
	    	<dl>
	    		<dt></dt>
	    		<dd><i><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/menu_icon01.png"></i><router-link to="index"></router-link></dd>
	    		<dd><i><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/menu_icon02.png"></i><router-link to="feilei"></router-link></dd>
	    		<dd><i><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/menu_icon03.png"></i><router-link to="gouwuche"></router-link></dd>
	    		<dd><i><img src="http://ydty.oss-cn-shanghai.aliyuncs.com/images/menu_icon04.png"></i><router-link to="my"></router-link></dd>
	    	</dl>
	    </div>
	    
	</div>
</header> 
 
 
    
  </div>
</template>

<script>
import $ from 'jquery'
import Swiper from 'swiper'
 
export default {

data () {
    return {

}
  },
  mounted(){
            $(function(){
				// $(".zy_title_top .r dl").hide()
				$(".zy_title_top .r span").mousedown(
					function(event){
						$(".zy_title_top .r dl").fadeToggle()
						event.stopPropagation();//
					}
				)
				$("html,body").mousedown(
					function(){
						$(".zy_title_top .r dl").fadeOut()
					}
				)
			});

  }
}
</script>

<style scoped>
 
</style>



